import React, {
    useEffect,
    useState,
} from 'react';
import {CSSTransition} from 'react-transition-group';
import {useAppSelector} from '../../../app/hooks';
import {selectGameState} from '../gameSlice';
import Cover from '../img/cover.jpg';
import styles from '../Quiz.module.css';
import {load} from '../utils';
import {Game} from './Game';
import {Header} from './Header';
import {Loader} from './Loader';
import {Result} from './Result';
import {Start} from './Start';

export const BasicWindow = ()=>{
    const {isFinish, isStart, isGameRun, isBegin} = useAppSelector(selectGameState);
    const [inProp, setInProp] = useState<boolean>(true);
    const [bg, setBg] = useState(Cover);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        setInProp(prev => !prev);
    }, [isStart])

    useEffect(() => {
        setInProp(false);
        load(bg, 200)
            .then( ()=> {
                setLoading(false);
                setInProp(true)
            }).catch(err => console.error(err));

    }, [bg])


    return <div className={styles.questionContainer} style={{ backgroundImage: `url(${bg})` }}>
        <Header />
        <CSSTransition in={!loading} timeout={1000} classNames="card">
        <CSSTransition in={inProp} timeout={1000} classNames="card">
                {loading ? <Loader/> :
                    <div className={`${styles.question} card ${isGameRun && styles.game}`}  style={{height: isBegin && !isFinish? '650px': 'auto'}}>

                        {isFinish && <Result/>}
                        {isStart && <Start/>}
                        {isGameRun && <Game/>}
                    </div>
                }
            </CSSTransition>
        </CSSTransition>
    </div>
}
