import {
    Container
} from '@mui/material';
import React, {
    useCallback,
} from 'react';
import { useAppDispatch } from '../../../app/hooks';
import {BeginList} from '../texts/begin';
import styles from '../Quiz.module.css'
import {
    startGame,
} from '../gameSlice';


export const Start = () => {
    const dispatch = useAppDispatch();

    const handleBeginButtonClick = useCallback(() => {
            dispatch(startGame())
    }, [dispatch]);


        const {question, answers} = BeginList[0];
        return <Container maxWidth="md">
                        <div  className = {styles.df}>
                            <div className={styles.questionText}>
                                <div dangerouslySetInnerHTML={{ __html: question || '' }} />
                            </div>
                        </div>
                        <ul className={styles.answerContainer} >
                            <li className={`${styles.answer}`} key={answers[0].id}>
                        <button onClick={handleBeginButtonClick}
                        >
                            <div dangerouslySetInnerHTML={{ __html: answers[0].text|| '' }}></div>
                        </button>
                            </li>
                        </ul>

                    </Container>
}
