import {useState} from 'react';
import styles from '../Quiz.module.css'
const code = 'BOLTHMARO_RIDE';

export const Code = () =>{
    const [isCopied, setIsCopied] = useState(false);

    const copyToClipBoard = (value: string) => {
        setIsCopied(true);
        navigator.clipboard.writeText(value).then(()=>{
            setIsCopied(true);
            setTimeout(()=>{
                setIsCopied(false);
            }, 1500);
        });
    };

    return <div className={styles.code}>
      <span>Ваш промокод: </span>   <button className={styles.button} onClick={()=>copyToClipBoard(code)}>{isCopied? 'скопійовано':code}</button>
    </div>
}
