import {
    createSlice,
    PayloadAction,
} from '@reduxjs/toolkit';
import {RootState} from '../../app/store';

interface GameState {
    isFinish: boolean,
    isStart: boolean,
    isGameRun: boolean,
    round: number,
    isBegin:boolean,
}

const initialState: GameState = {
    isFinish: false,
    isStart: true,
    isGameRun: false,
    round: 1,
    isBegin:false,
};


export const gameSlice = createSlice({
    name: 'boltSimulator',
    initialState,
    reducers: {
        goToStart: (state, {payload}: PayloadAction<number>) =>({...initialState, round: payload}),
        goToResult: (state) =>{
            state.isFinish = true
            state.isGameRun = false
            state.isStart = false
        },
        startGame: (state) =>{
            state.isFinish = false
            state.isGameRun = true
            state.isStart = false
        },
        setBegin: (state, {payload}) =>{
            state.isBegin = payload
        },

    },
});

export const {goToStart, goToResult,  startGame, setBegin} = gameSlice.actions;

export const selectGameState = ({game}:RootState) => game;

export default gameSlice.reducer;
