import StarIcon from '@mui/icons-material/Star';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import Obs1 from './img/obstacles/01.png'
import Obs2 from './img/obstacles/02.png'
import Obs3 from './img/obstacles/03.png'
import Obs4 from './img/obstacles/04.png'
import Obs5 from './img/obstacles/05.png'
import Obs6 from './img/obstacles/06.png'
import Obs7 from './img/obstacles/07.png'
import Obs8 from './img/obstacles/08.png'

import {
    MAX_RATE,
    OBSTCLESAMOUNT,
} from './const';
import styles from './Quiz.module.css';


const OBSTACLES = [Obs1, Obs2, Obs3, Obs4, Obs5, Obs6, Obs7, Obs8];

export function returnIcons(rate: number) {
    return  Array(MAX_RATE).fill(MAX_RATE).map((item, index) => rate && index>= rate ?
        <StarBorderIcon/>:
        <StarIcon className={styles.colorfull}/>)
}

export const loadImage = (url: string) => new Promise((resolve, reject) => {
    const img = new Image();
    img.addEventListener('load', () => resolve(img));
    img.addEventListener('error', (err) => reject(err));
    img.src = url;
});

export function createDelay(ms: number) {
    return new Promise((resolve, reject) => {
        setTimeout(resolve, ms);
    });
}


export async function load (url: string, delay: number){
    await  Promise.all([loadImage(url), createDelay(delay)]);
}


export function getRandImg() {
    let randInt = Math.floor(Math.random() * OBSTACLES.length);
    let url = OBSTACLES[randInt];
    return `url('${url}') top/contain no-repeat`;
}


// @ts-ignore
export function animate({timing, duration, element, draw}) {

    let start = performance.now();

    requestAnimationFrame(function animate(time) {
        // timeFraction изменяется от 0 до 1
        let timeFraction = (time - start) / duration;
        if (timeFraction > 1) timeFraction = 1;

        // вычисление текущего состояния анимации
        let progress = timing(timeFraction);

        draw(progress, element); // отрисовать её

        if (timeFraction < 1) {
            requestAnimationFrame(animate);
        }

    });
}


export function randPlace(){
    const array = [0, 102, 102, 210];
    const randomIndex = Math.floor(Math.random() * array.length);
    return array[randomIndex];
}

export function randomIntFromInterval(min: number, max: number) { // min and max included
    return Math.floor(Math.random() * (max - min + 1) + min)
}
