import {
    useCallback,
    useEffect,
    useRef,
    useState,
} from 'react';
import {useAppDispatch} from '../../../app/hooks';
import {
    goToResult, setBegin,
} from '../gameSlice';
import styles from '../Quiz.module.css'
import ButtonLeft from '../img/button_left.png';
import ButtonRight from '../img/button_right.png';
import ButtonLeftPressed from '../img/button_left_pressed.png';
import ButtonRightPressed from '../img/button_right_pressed.png';
import {
    animate,
    getRandImg,
    loadImage,
    randPlace,
} from '../utils';
import {Counter} from './Counter';
import Scooter3 from '../img/scooters/3.png'
import Scooter4 from '../img/scooters/4.png'
import Scooter5 from '../img/scooters/5.png'
import Scooter6 from '../img/scooters/6.png'
import Lvl1 from '../img/stripes.png';
import Lvl2 from '../img/brusch.jpg';
import Lvl3 from '../img/grass_long_screen.png';
import staticLvl3 from '../img/static_back_for_grass_bw.png';
import { scrollIntoView } from 'seamless-scroll-polyfill';
import Obs1 from '../img/obstacles/01.png'
import Obs2 from '../img/obstacles/02.png'
import Obs3 from '../img/obstacles/03.png'
import Obs4 from '../img/obstacles/04.png'
import Obs5 from '../img/obstacles/05.png'
import Obs6 from '../img/obstacles/06.png'
import Obs7 from '../img/obstacles/07.png'
import Obs8 from '../img/obstacles/08.png'




const initSettings = {
    start: false,
    roundPlace: true,
    dtp: false,
    round: 1,
    enemies: 2,
    traffic: 3,
    duration: window.innerWidth>980?1200:1800
};

const initialEnemies = [{
    bg: getRandImg(),
    id: Math.random(),
    positionX: 0,
    positionY: 0,
}]


export const Game = ()=> {

    const dispatch = useAppDispatch();
    const [settings, setSettings] = useState(initSettings);
    const [position, setPosition] = useState(     -50);
    const [seconds, setSeconds] = useState(0);
    const [isLeftButtonPressed, setIsLeftButtonPressed] = useState(false);
    const [isRightButtonPressed, setIsRightButtonPressed] = useState(false);
    const [enemies, setEnemies] = useState(initialEnemies);
    const [bgArray, setBgArray] = useState(null);
    const [toAddNew, setToAddNew] = useState(false);
    const [toAnimate, setToAnimate] = useState(null);
    const [startCountDown, setStartCountDown] = useState(false);
    const scooter = useRef(null);
    const buttonLeft = useRef(null);
    const gameFooterRef = useRef(null);
    const gameHeaderRef = useRef(null);
    const enemiesRefs = useRef([]);

    useEffect(()=>{
        loadImage(Scooter3).catch(e=> console.log(e));
        loadImage(Scooter4).catch(e=> console.log(e));
        loadImage(Scooter5).catch(e=> console.log(e));
        loadImage(Scooter6).catch(e=> console.log(e));
        loadImage(Lvl1).catch(e=> console.log(e));
        loadImage(Lvl2).catch(e=> console.log(e));
        loadImage(Lvl3).then(()=>{
            // @ts-ignore
            setBgArray([Lvl1, Lvl2, Lvl3])
        }).catch(e=> console.log(e));
        loadImage(staticLvl3).catch(e=> console.log(e));
        loadImage(ButtonLeft).catch(e=> console.log(e));
        loadImage(ButtonRight).catch(e=> console.log(e));
        loadImage(ButtonRightPressed).catch(e=> console.log(e));
        loadImage(ButtonLeftPressed).catch(e=> console.log(e));

        loadImage(Obs1).catch(e=> console.log(e));
        loadImage(Obs2).catch(e=> console.log(e));
        loadImage(Obs3).catch(e=> console.log(e));
        loadImage(Obs4).catch(e=> console.log(e));
        loadImage(Obs5).catch(e=> console.log(e));
        loadImage(Obs6).catch(e=> console.log(e));
        loadImage(Obs7).catch(e=> console.log(e));
        loadImage(Obs8).catch(e=> console.log(e));
    }, [])

    useEffect(()=>{
        if(settings.start){
            // @ts-ignore
            scrollIntoView(gameFooterRef?.current, { behavior: "smooth", block: 'end' });
        };

    }, [settings.start])

    useEffect(()=>{
        if(settings.roundPlace || settings.dtp){
            // @ts-ignore
            scrollIntoView(gameHeaderRef?.current, { behavior: "smooth", block: 'start' });
        };

    }, [settings.dtp, settings.roundPlace])

    useEffect(() => {
        if(settings.start){
            // @ts-ignore
            buttonLeft?.current?.focus();
            let  timesRun = 0;
        const interval = setInterval(() => {
            setSeconds(prev=>prev+1);
            timesRun += 1;
            if(timesRun === 30){
                if(settings.round === 3){
                    dispatch(goToResult());
                }
                clearInterval(interval);
                setSettings(prevState => ({...initSettings, round: prevState.round+1, start: false, roundPlace: false}))
                setSeconds(0);
            }
        }, 1000);
        return () => clearInterval(interval);
        }
    }, [ settings.start]);
    useEffect(() => {
        if(settings.start) {
            const time = settings.round<3?settings.duration/2:settings.duration ;
            // @ts-ignore
            setToAnimate(initialEnemies[0]);
            setTimeout(() => {
                const newEnemy = {
                    positionX: randPlace(),
                    positionY: -30,
                    bg: getRandImg(),
                    id: Math.random(),
                }
                setEnemies(prev => ([...prev, newEnemy]));
                // @ts-ignore
                setToAnimate(newEnemy);
            }, time)
            if(settings.round>1){
                setTimeout(() => {
                    const newEnemy = {
                        positionX: randPlace(),
                        positionY: -30,
                        bg: getRandImg(),
                        id: Math.random(),
                    }
                    setEnemies(prev => ([...prev, newEnemy]));
                    // @ts-ignore
                    setToAnimate(newEnemy);
                }, time*2)
            }
            // if(settings.round>2){
            //     setTimeout(() => {
            //         const newEnemy = {
            //             positionX: randPlace(),
            //             positionY: -30,
            //             bg: getRandImg(),
            //             id: Math.random(),
            //         }
            //         setEnemies(prev => ([...prev, newEnemy]));
            //         // @ts-ignore
            //         setToAnimate(newEnemy);
            //     }, time*3)
            // }
        }
    },[settings.start]);

    const runAnimation = useCallback((elementFromBase: any) => {
        const index = enemies.findIndex(item => item.id === elementFromBase.id)
        animate({
            duration: settings.duration,
            timing: (timeFraction: any) => {
                return timeFraction
            },
            element: enemiesRefs?.current[index],
            draw: (progress: any, element: any) => {
                if(element?.style) {
                    element.style.top = `${Math.floor((progress * 650 -150))}px`;
                    element.style.transform = `translateX(${elementFromBase.positionX}%)`
                }
                if(progress >= 1){
                    setTimeout(()=>{
                        setToAddNew(true);
                    },settings.round<3?settings.duration/(settings.round*0.65): settings.duration/(settings.round*0.65))
                }
            }})
    }, [enemies, settings.round]);


    useEffect(()=>{
        if(settings.start && toAddNew) {
            const newEnemy = {
                positionX: randPlace(),
                positionY: -30,
                bg: getRandImg(),
                id:Math.random(),
            }
            setEnemies(prev=> [...prev, newEnemy]);
            // setToRemove(null);
            // @ts-ignore
            setToAnimate(newEnemy);
            setToAddNew(false);
        }
        // @ts-ignore
    }, [settings.enemies, settings.start, toAddNew])

    useEffect(()=>{
        if (!settings.start ||!toAnimate ) {
            return;
        }
        runAnimation(toAnimate);
    }, [runAnimation, settings.start, toAnimate])

    useEffect(()=>{
        if(settings.start) {
            enemies.forEach((enemy, index) => {
                // @ts-ignore
                const scooterRect = scooter?.current?.getBoundingClientRect();
                // @ts-ignore
                const enemyRect = enemiesRefs?.current[index]?.getBoundingClientRect();
                if (
                    scooterRect.top <= enemyRect.bottom &&
                    scooterRect.right >= enemyRect.left &&
                    scooterRect.left <= enemyRect.right &&
                    scooterRect.bottom >= enemyRect.top
                ) {
                    console.warn("ДТП");
                    setSettings(prev=>({...prev, dtp: true, start: false}));
                    setSeconds(0);
                    console.log(scooterRect, enemyRect);
                }
            });
        }
    })
    useEffect(()=> {
        dispatch(setBegin(settings.start))
    }, [dispatch, settings.start]);

    const startRound = useCallback(()=>{
        setSettings(prev=> ({...initSettings, start: true, dtp:false, round:prev.round, roundPlace: false }));
        setEnemies(initialEnemies);
        setToAddNew(false);
        setToAnimate(null);
        setStartCountDown(false)
    }, []);

    const scooterLeft = useCallback(()=>{
        setPosition(prev=>prev>-100 ? prev-103: prev);
        setIsLeftButtonPressed(true);
        setTimeout(()=>{ setIsLeftButtonPressed(false);}, 200)
    }, []);
    const scooterRight = useCallback(()=>{
        setPosition(prev=>prev<50? prev+103: prev);
        setIsRightButtonPressed(true);
        setTimeout(()=>{ setIsRightButtonPressed(false);}, 200)
    }, []);

    const keyboardEvents = useCallback((evt:any) => {
        switch (evt.keyCode) {
            case 37:
            case 65:
                scooterLeft();
                break;
            case 39:
            case 68:
                scooterRight();
                break;
        }
    }, [scooterLeft, scooterRight]);


    const handleGoButtonClick= ()=>{
        if(settings.dtp){
            setSettings(prevState => ({...prevState, roundPlace: true, dtp: false}))
        }
        if(!settings.start && !settings.roundPlace){
            setSettings(prevState => ({...prevState, roundPlace: true}))
        }
        if(!settings.start && settings.roundPlace){
            setStartCountDown(true);
        }
    }

    return <div className={styles.gameInside}>
        <div className={styles.gameHeader} ref={gameHeaderRef}>
            <div className={styles.time}>
                <span>Час поїздки</span>
                <b>{seconds} секунд</b>
            </div>
            <div className={styles.amount}>
                <span>Рівень</span>
                <b>{settings.round} із 3</b>
            </div>
        </div>

        {settings.roundPlace && settings.round === 1 && <div className={styles.dtp}>
            <div className={styles.gameh1}>Рівень 1</div>
            <div className={styles.gameUnder}>Виїжджаємо зі свого будинку і прямуємо до станції метро. Але спершу перевіримо
                безпеку та одягнемо шолом.
            </div>
            <p className={styles.gameText}>
                <b> 1.Перевірка безпеки</b>
                <img src={Scooter5} alt=""/>
            </p>
            <ul className={styles.gameText}>
                <li>До початку поїздки перевірте справність самоката.</li>
                <li>Переконайтеся, що шини и накачані та перебувають у належному стані</li>
                <li>Що гальма  працюють як треба</li>
                <li>Важіль акселератора працює, і ви можете прискоритися</li>
                <li>Акумулятор має достатній заряд</li>
                <li>Самокат не пошкоджений і не видає дивних звуків під час поїздки</li>
            </ul>
                <p className={styles.gameText}>
                    <b> 2. Одягніть шолом</b>
                    <img src={Scooter4} alt=""/>
                </p>

        </div>
        }
        {settings.roundPlace && settings.round === 2 && <div className={styles.dtp}>
            <div className={styles.gameh1}>Рівень 2</div>
            <div className={styles.gameUnder}>Прямуємо від станції метро до центру міста
            </div>
        </div>
        }
        {settings.roundPlace && settings.round === 3 && <div className={styles.dtp}>
            <div className={styles.gameh1}>Рівень 3</div>
            <div className={styles.gameUnder}>Їдемо центром міста
            </div>
        </div>
        }



        {startCountDown && <Counter fn={startRound}/>}


        {!settings.start && settings.round===2 &&  !settings.roundPlace  && !settings.dtp && <div className={styles.dtp}>
            <div className={styles.gameh1}>Перемога!</div>
            <div className={styles.gameUnder}>Ви успішно пройшли <b>1</b> рівень з <b>3</b>! </div>
            <p className={styles.gameText}>
                <b>Прості правила зроблять поїздку безпечною для вас та інших учасників дорожнього руху:</b>
                <img src={Scooter6} alt=""/>
            </p>
            <ol className={styles.gameText}>
                <li>Не катайтесь на самокаті в парі або групою. Це небезпечно та може призвести до травмування.</li>
                <li>Ніколи не забувайте одягнути шолом.</li>
                <li>Користуйтесь самокатами тільки у тверезому стані. Керувати транспортним засобом під впливом алкоголю незаконно та заборонено.</li>
                <li>Використовуйте для паркування спеціально відведені місця та не залишайте самокат там, де він може заважати іншим. Наприклад, блокувати тротуар, перекривати входи/виходи чи зупинки громадського транспорту</li>
            </ol>
        </div>
        }

        {!settings.start && settings.round===3 &&  !settings.roundPlace && !settings.dtp && <div className={styles.dtp}>
            <div className={styles.gameh1}>Перемога!</div>
            <div className={styles.gameUnder}>Ви успішно пройшли <b>2</b> рівень з <b>3</b>! </div>
            <p className={styles.gameText}>
                <b>Прості правила зроблять поїздку безпечною для вас та інших учасників дорожнього руху:</b>
                <img src={Scooter3} alt=""/>
            </p>
            <ol className={styles.gameText} start={5}>
                <li>Не забувай висувати підніжку, щоб самокат стояв вертикально і не падав.</li>
                <li>До початку поїздки перевірте справність самоката</li>
                <li>Дотримуйся правил дорожнього руху та з повагою ставтеся до пішоходів та інших транспортних засобів.</li>
                <li>Якщо ви користуєтесь самокатом вперше, рекомендуємо спочатку потренуватися на рівній дорозі з мінімальним рухом. «Режим початківця», який є у самокатах від компанії Bolt, обмежує швидкість на перші 5 поїздок до 15 км/год.</li>
            </ol>
        </div>
        }

        {settings.dtp && <div className={styles.dtp} >
            <div className={`${styles.gameh1} ${styles.red}`}>Упс, зіткнення...</div>
        </div>
        }

        {settings.start && <div className={styles.gameArea} style={settings.round === 3 ? {backgroundImage: `url(${staticLvl3})`}: {}}>

            <div className={`${styles.bg} ${settings.start && styles.run}`}
                 style={{animationDuration: `${settings.duration}ms`, background: `url(${bgArray && bgArray[settings.round-1]}) repeat-y center`}}></div>


            <div className={`${styles.gameField}`}>
                <div className={styles.scooter} style={{transform: `translateX(${position}%)`}} ref={scooter}/>
                {settings.start && enemies.map((item, index) => {
                    return <div className={styles.obstacle}
                                key={item.id}
                                ref={el => {
                                    // @ts-ignore
                                    return enemiesRefs.current[index] = el
                                }}
                                style={{
                                    background: item.bg,
                                }}>
                    </div>
                })
                }
            </div>
        </div>
        }



        <div className={styles.gameFooter} onKeyDown={keyboardEvents} ref={gameFooterRef}>
            {settings.start && <>
                <button className={styles.gameButton}
                        ref = {buttonLeft}
                        onClick={scooterLeft}
                        style={{backgroundImage: `url(${isLeftButtonPressed? ButtonLeftPressed: ButtonLeft})`}}
                        tabIndex={0}
                />
                <button className={`${styles.gameButton} ${styles.gameButtonRight}`}
                        onClick={scooterRight}
                        style={{backgroundImage: `url(${isRightButtonPressed? ButtonRightPressed: ButtonRight})`}}
                        tabIndex={0}
                />
            </>}
            {!settings.start  &&
                <button className={styles.button} onClick={handleGoButtonClick}>
                    {settings.roundPlace && settings.round ===1 && 'Все готово. Виїжджаю!'}
                    {settings.roundPlace  && settings.round >1 && 'Виїжджаю!'}
                    {settings.dtp && 'Починаю заново!'}
                    {settings.round >1 && !settings.dtp && !settings.roundPlace  && 'Далі!'}
                </button>
            }

        </div>
        </div>
}
