import {
    useEffect,
    useRef,
    useState,
} from 'react';
import styles from '../Quiz.module.css';

// @ts-ignore
export const Counter = (props)=>{

    const [count, setCount] = useState(4);
    const [bigStyle, setBigStyle] = useState(false);


    useEffect(()=>{
        let counter = 3
        setCount(counter);
        setBigStyle(true);
        setTimeout(()=>{
            setBigStyle(false);
        }, 1000);
        counter--;

        const nIntervId = setInterval(()=>{
                setCount(counter);
                setTimeout(()=>{
                    setBigStyle(true);
                    console.log('here')
                }, 1)
            setTimeout(()=>{
                setBigStyle(false);
            }, 1000)
                counter--;
            if(counter<0){
                clearInterval(nIntervId)
                props.fn();
            }
        }, 1200)
    }, [])


    return <div className={styles.vidlik}>
        {<div className={`${styles.countDown} ${bigStyle ? styles.Big:''}`} >{count}</div>}
    </div>;
}
